<template>
  <div>
    <div class="profile__wrapper">
      <el-card class="profile">
        <div slot="header" class="profile__title">
          <h2 class="profile-title">{{ user.name }}</h2>
          <h3 class="profile-name" v-if="isUser">Мой профиль</h3>
        </div>
        <div class="text item">
          <picture>
            <source
              :srcset="user.profile_image.medium"
              media="(max-width: 768px)"
            />
            <source
              :srcset="user.profile_image.small"
              media="(max-width: 520px)"
            />
            <img :src="user.profile_image.large" alt="" />
          </picture>
        </div>
      </el-card>
    </div>
    <div v-if="$store.getters.isError.length > 0">
      <Error />
    </div>
  </div>
</template>

<script>
import { toJson } from "unsplash-js";
import unsplash from "../plugins/auth-unsplash";

export default {
  name: "User",
  data() {
    return {
      user: "",
      isUser: false,
    };
  },
  created() {
    unsplash.currentUser
      .profile()
      .then(toJson)
      .then(json => {
        localStorage.setItem("User", JSON.stringify(json));
        if (this.$route.params.id == json.username) {
          this.isUser = true;
          this.user = json;
        } else {
          unsplash.users
            .profile(this.$route.params.id)
            .then(toJson)
            .then((json) => {
              if (json.errors != undefined && json.errors[0] == "Couldn't find User") {
                this.$message.error("Данного пользователя не существует. Вы будете перенаправлены на главную страницу.")
                setTimeout(() => this.$router.push("/"), 2000)
              } else {
                this.user = json
                this.isUser = false
                // console.log("err", json)
              }
            })
            .catch(err => this.$store.commit("setError", err))
        }
      })
      .catch(err => this.$store.commit("setError", err))
  },
};
</script>

<style lang="scss" scoped>
.profile {
  margin: 50px auto;
  max-width: 970px;
  &__wrapper {
    margin: 0 20px;
  }
  &__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-title {
    font-size: clamp(14px, 6vw, 24px);
    margin-right: 20px;
    text-align: center;
  }
}
@media (max-width: 461px) {
  .profile {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .profile-title {
    margin-right: 0;
  }
  .profile-name {
    display: none;
  }
}
</style>
